<template>
  <v-app>
    <v-main>
      <h1 class="text-center">Proximamente</h1>
      <v-container>
        <v-row
          justify="center"
          v-for="(item, index) in listProximamente"
          :key="index"
        >
          <v-col cols="12" sm="5">
            <v-card elevation="5">
              <v-img max-height="200" contain :src="item['portada']"></v-img>
              <v-card-title>{{ item["title"] }}</v-card-title>
              <v-divider></v-divider>
              <v-card-actions>
                <v-spacer></v-spacer>
                <v-btn fab small color="accent">
                  <v-icon color="white">mdi-pencil</v-icon>
                </v-btn>
                <v-btn fab small color="red">
                  <v-icon color="white" @click="deleted(item)"
                    >mdi-delete</v-icon
                  >
                </v-btn>
              </v-card-actions>
            </v-card>
          </v-col>
        </v-row>
      </v-container>
    </v-main>
    <DialogAdd :refresh="getProximamente"></DialogAdd>
    <DialogDeleted
      :item="itemSelected"
      v-if="deleteDialog"
      :toggle="toggleDialog"
      :refresh="getProximamente"
    ></DialogDeleted>
  </v-app>
</template>

<script>
import axios from "../services/axios";
import DialogAdd from "../components/Proximamente/DialogAdd";
import DialogDeleted from "../components/Proximamente/DialogDelete";

export default {
  data: () => {
    return {
      listProximamente: [],
      deleteDialog: false,
      itemSelected: null,
    };
  },
  methods: {
    getProximamente() {
      axios.get("/api/proximamente").then((res) => {
        this.listProximamente = res.data;
      });
    },
    deleted(item) {
      this.itemSelected = item;
      this.toggleDialog();
    },
    toggleDialog() {
      this.deleteDialog = !this.deleteDialog;
    },
  },
  created() {
    this.getProximamente();
  },
  components: { DialogAdd, DialogDeleted },
};
</script>