<template>
  <div class="text-center">
    <v-dialog v-model="showDialog" persistent max-width="600px">
      <v-card :disabled="isRequest" :loading="isRequest">
        <!-- <LoadAnimation></LoadAnimation> -->
        <v-card-title
          ><span class="text-h5">Agregar Proximamente</span>
        </v-card-title>
        <v-divider></v-divider>
        <v-card-text>
          <v-row>
            <v-container>
              <v-row class="mt-0">
                <v-col cols="12" lg="12" sm="6" md="4">
                  <v-form ref="form">
                    <v-text-field
                      required
                      label="Titulo"
                      outlined
                      :rules="requiredParam"
                      v-model="dataForm.title"
                    ></v-text-field>
                    <v-file-input
                      required
                      counter
                      show-size
                      truncate-length="10"
                      accept="image/*"
                      label="Portada"
                      outlined
                      :rules="requiredParam"
                      @change="(e) => (dataForm.portada = e)"
                    ></v-file-input>
                    <v-textarea
                      required
                      outlined
                      label="Descripcion"
                      :rules="requiredParam"
                      v-model="dataForm.description"
                    ></v-textarea>
                  </v-form>
                </v-col>
              </v-row> </v-container
          ></v-row>
        </v-card-text>
        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn color="blue darken-1" text @click="toggleDialog()">
            Cerrar
          </v-btn>
          <v-btn color="blue darken-1" class="white--text" @click="saved()">
            Guardar
          </v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>

    <v-btn
      fab
      dark
      small
      color="blue"
      fixed
      right
      bottom
      @click="toggleDialog()"
    >
      <v-icon>mdi-plus</v-icon>
    </v-btn>
  </div>
</template>

<script>
import axios from "../../services/axios";
import LoadAnimation from "../loadAnimation";
export default {
  data: () => {
    return {
      valid: true,
      isRequest: false,
      showDialog: false,
      requiredParam: [(v) => !!v || "Parametro es requerido"],
      dataForm: {
        title: null,
        description: null,
        portada: null,
      },
    };
  },
  methods: {
    toggleDialog() {
      this.showDialog = !this.showDialog;
    },
    saved() {
      if (this.$refs.form.validate()) {
        this.isRequest = true;
        let form = new FormData();
        form.append("title", this.dataForm.title);
        form.append("description", this.dataForm.description);
        form.append("portada", this.dataForm.portada);
        axios.post("/api/proximamente", form).then((res) => {
          this.isRequest = false;
          if (res.data["status"] == 200) {
            this.refresh();
            this.toggleDialog();
          }
        });
      }
    },
  },
  components: {
    LoadAnimation,
  },
  props: ["refresh"],
};
</script>