<template>
  <div class="text-center">
    <v-dialog v-model="showModal" persistent max-width="400">
      <v-card :loading="isRequest" :disabled="isRequest">
        <v-card-title>
          <span class="text-h5">Eliminar elemento</span>
        </v-card-title>
        <v-divider></v-divider>

        <v-card-actions>
          <v-spacer> </v-spacer>
          <v-btn color="error" @click="deleted()"> Eliminar </v-btn>
          <v-btn color="primary" @click="toggle"> Cancelar </v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
  </div>
</template>

<script>
import axios from "../../services/axios";
export default {
  data: () => {
    return {
      showModal: true,
      isRequest: false,
    };
  },
  methods: {
    deleted() {
      this.isRequest = true;
      axios.delete("/api/proximamente/" + this.item["id"]).then((res) => {
        this.isRequest = false;
        if (res.data["status"] == 200) {
          this.refresh();
          this.toggle();
        }
      });
    },
  },
  props: ["toggle", "item", "refresh"],
};
</script>